import React from 'react';
import { Picker, Popup, PopupPicker } from 'easy-picker';
import { PickerAddress } from '../picker-address';
import './index.scss';

const userData = {
  data1: [
    { name: '', value: 0 },
    { name: '', value: 1 },
    { name: '', value: 2 },
    { name: '', value: 3 },
    { name: '', value: 4 },
    { name: 'Lincal', value: 5 },
    { name: '', value: 6 },
    { name: 'Raoh', value: 7 },
    { name: '', value: 8 },
    { name: '', value: 9 },
    { name: '', value: 10 },
    { name: '', value: 11 }
  ],
  data2: [
    { name: '', value: 0 },
    { name: '', value: 1 },
    { name: '', value: 2 },
    { name: '', value: 3 },
    { name: '', value: 4 },
    { name: 'Lincal', value: 5 },
    { name: '', value: 6 },
    { name: 'Raoh', value: 7 },
    { name: '', value: 8 },
    { name: '', value: 9 },
    { name: '', value: 10 },
    { name: '', value: 11 }
  ]
};

export default class PickerDemo extends React.Component {
  constructor() {
    super();

    this.state = {
      userPickerVisible: false,
      userPickerVisible2: false,
      addressPickerVisible: false,
      selectedValue: {
        data1: 4,
        data2: 5
      },
      // picker<PopupPicker/>, 
      cacheSelectedValue1: 4,
      // 
      showAddrPicker:false,
      selectedAddrName: {}
    };
  }

  // user
  showUserPicker(e) {
    e.nativeEvent.stopImmediatePropagation();
    this.setState({ userPickerVisible: true });
  }
  // user
  showUserPicker2(e) {
    e.nativeEvent.stopImmediatePropagation();
    this.setState({ userPickerVisible2: true });
  }

  handleChangeUser(value) {
    this.setState({ selectedValue: value });
  }

  closeUserPicker() {
    this.setState({
      userPickerVisible: false,
      selectedValue: {
        ...this.state.selectedValue,
        data1: this.state.cacheSelectedValue1
      }
    });
  }

  cancelUserPicker() {
    this.setState({
      userPickerVisible: false
    });
  }

  render() {
    return (
      <main className="picker-demo">
        <header>
          <p>{JSON.stringify(
            PopupPicker.getName(userData, this.state.selectedValue)
          )}</p>
          <p>{JSON.stringify(this.state.selectedAddrName)}</p>
        </header>
        <p>
          
          <code>{`<Popup><Picker/></Popup>`}</code>
          
          <code>{`<PopupPicker/>`}</code>
        </p>

        <section className="button-wrap">
          <button
            type="button"
            onClick={this.showUserPicker.bind(this)}
            className="btn button-primary"
          >
            {``}
          </button>

          <button
            type="button"
            onClick={this.showUserPicker2.bind(this)}
            className="btn button-primary"
          >
            {``}
          </button>

          <button
            type="button"
            onClick={()=>{this.setState({
              showAddrPicker:true
            })}}
            className="btn button-primary"
          >
            
          </button>
        </section>

        <Popup
          onCancel={this.cancelUserPicker.bind(this)}
          onConfirm={this.closeUserPicker.bind(this)}
          visible={this.state.userPickerVisible}
        >
          <Picker
            onChange={value =>
              this.setState({
                cacheSelectedValue1: value
              })
            }
            data={userData.data1}
            selectedValue={this.state.cacheSelectedValue1}
          />
        </Popup>
        <PopupPicker
          data={userData}
          selectedValue={this.state.selectedValue}
          visible={this.state.userPickerVisible2}
          liveUpdate={false}
          onCancel={() => this.setState({ userPickerVisible2: false })}
          onSelect={selectedValue =>
            this.setState({
              userPickerVisible2: false,
              selectedValue
            })
          }
          onChanging={(selectedValue, key, value, name) => {
            console.log(selectedValue, key, value, name);
          }}
        />
        <PickerAddress
          visible={this.state.showAddrPicker}
          onCancel={()=>this.setState({
            showAddrPicker:false,
          })}
          onSelect={(oVal, oName)=>{
            this.setState({
              selectedAddrName: oName,
              showAddrPicker:false,
            })
          }}
        />
      </main>
    );
  }
}



